import React from 'react'
import { useParams } from 'react-router-dom'
import styled from 'styled-components'

const ProjectGrid = styled.div`
  display: flex;
  column-gap: 5%;
  row-gap: 20px;
  flex-wrap: wrap;
  margin-top: 100px;
  padding-bottom: 30px;
  position: relative;
  z-index: 1;
`

const ProjectView = () => {
  const { name } = useParams();

  return (
    <ProjectGrid>
      <h1>{name}</h1>
      
    </ProjectGrid>
  )
}

export default ProjectView