import Profile from 'Assets/Images/Profile.jpg'
import { useLanguage } from 'Contexts/LanguageContext';
import styled from 'styled-components'


const AboutMeContainer = styled.div`
  display: flex;
  justify-content: ;
  align-items: flex-start;
  gap: 20px;

  div {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  img {
    width: fit-content;
    height: fit-content;
    max-width: 100%;
    max-height: 400px;
    box-shadow: 2px 2px 10px var(--shadow);
  }
`;

const About = () => {
  const { texts, loading } = useLanguage();

  console.log(texts);

  return (
    <AboutMeContainer className='container'>
      <img src={Profile} alt="" />
      <div>
        <p>Meu nome  Marcelo, sou desenvolvedor FullStack e empreendedor.</p> 
        <p>Eu estou envolvido com programao desde os 15 anos, isto , 6 anos como entusiasta e 3 anos como profissional na rea.</p>
        <p>Meu idioma nativo  Portugus (Brasil), sei falar tambm Ingls, Espanhol e estou aprendendo Russo. , !</p>
        <p>Decidi criar este site para compilar meus portflios profissionais, projetos pessoais e tambm falar um pouco sobre mim.</p>
        <p>Minha vasta quantidade de conhecimentos variados, que no incluem apenas programao e cdigo, mas tambm skills como Comunicao Segura, Feedback Efetivo e tantas outras reas (Criao de Vdeo, E-Commerce, Marketing e Ensino Prtico.)</p>
      </div>
      <div>
        <h1>{texts['About Me']}</h1>
      </div>
    </AboutMeContainer>
  )
}

export default About